/* =====================
  header
===================== */
.header {
  padding: 10px 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 1;
  width: 100%;
  background-color: #fff;
}
@media only screen and (min-width: 960px) {
  .header {
    width: 300px;
    height: 100vh;
    display: block;
    padding: 40px 45px;
  }
}
.header .header_logo {
  width: 180px;
}
@media only screen and (min-width: 960px) {
  .header .header_logo {
    width: 200px;
  }
}
.header .icon_wrapper {
  padding: 10px 0;
  z-index: 1;
}
@media only screen and (min-width: 960px) {
  .header .icon_wrapper {
    display: none;
  }
}
.header .icon_wrapper:hover {
  cursor: pointer;
}
.header .menu_icon {
  width: 30px;
  height: 2px;
  background-color: #000;
  position: relative;
  transition: background-color 0.3s linear;
}
.header .menu_icon::before, .header .menu_icon::after {
  content: "";
  display: block;
  width: 30px;
  height: 2px;
  background-color: #000;
  position: absolute;
  transition: transform 0.3s ease-out;
  transform-origin: center;
}
.header .menu_icon::before {
  top: -10px;
}
.header .menu_icon::after {
  top: 10px;
}
@media only screen and (max-width: 959px) {
  .header .menu_content {
    background-color: #fff;
    width: 300px;
    height: 100vh;
    position: fixed;
    left: 0px;
    top: 0;
    padding: 25px;
    transform: translateX(-300px);
    opacity: 0;
    overflow: hidden;
    transition: transform 0.3s linear, opacity 0.3s linear;
  }
}
@media only screen and (min-width: 960px) {
  .header .menu_content {
    margin-top: 38px;
  }
}
.header .menu_content .menu_item {
  font-size: 14px;
  line-height: 1.7142857143;
  margin-top: 16px;
}
.header .menu_content .sns_list {
  margin-top: 78px;
}
.header .menu_content .sns_item {
  font-size: 14px;
  line-height: 1.7142857143;
  margin-top: 16px;
}
.header.is-open .mask {
  display: block;
}
.header.is-open .menu_icon {
  background-color: transparent;
}
.header.is-open .menu_icon::before {
  background-color: #fff;
  transform: rotate(-315deg) translate(7px, 7px);
}
.header.is-open .menu_icon::after {
  background-color: #fff;
  transform: rotate(315deg) translate(7px, -7px);
}
@media only screen and (max-width: 959px) {
  .header.is-open .menu_content {
    transform: translateX(0px);
    opacity: 1;
    overflow: visible;
  }
}

/* =====================
  END header
===================== */
.mask {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
}

@media only screen and (min-width: 960px) {
  .content_wrapper {
    margin-left: 300px;
  }
}

