.mainVisual {
  height: 70vh;
}
@media only screen and (min-width: 960px) {
  .mainVisual {
    height: 100vh;
  }
}
.mainVisual .mainVisual_video {
  height: 600px;
  position: fixed;
  z-index: 0;
}
@media only screen and (min-width: 960px) {
  .mainVisual .mainVisual_video {
    width: 100vw;
    object-fit: cover;
  }
}

