@media only screen and (min-width: 960px) {
  .introduction .introduction_wrapper {
    max-width: 900px;
    margin: auto;
  }
}
.introduction .introduction_title {
  text-align: center;
  font-size: 20px;
  line-height: 1.05;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
  text-shadow: 0px 0px 2px rgba(13, 13, 13, 0.1607843137);
}
@media only screen and (min-width: 960px) {
  .introduction .introduction_title {
    font-size: 28px;
  }
}
.introduction .introduction_title::before, .introduction .introduction_title::after {
  content: "";
  width: 20%;
  height: 1px;
  background-color: #fff;
}
@media only screen and (min-width: 960px) {
  .introduction .introduction_title::before, .introduction .introduction_title::after {
    width: 28%;
  }
}
@media only screen and (min-width: 960px) {
  .introduction .introduction_list {
    display: flex;
