.scroll {
  width: 50px;
  height: 50px;
  border: 1px solid #fff;
  border-radius: 50%;
  background-color: #121212;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  position: fixed;
  bottom: 25px;
  right: 25px;
  opacity: 0;
  overflow: hidden;
  transition: opacity 0.3s linear;
}
.scroll::after {
  content: "";
  border-style: solid;
  border-color: transparent transparent #fff transparent;
  border-width: 0px 7px 7px 7px;
  display: inline-block;
}
.scroll.is-show {
  opacity: 1;
  overflow: visible;
}

/* =====================
  END scroll
===================== */
/* =====================
  footer
===================== */
.footer {
  background-color: #121212;
  color: #fff;
  padding: 40px 0 20px;
  position: relative;
  z-index: 0;
}
.footer .footer_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.footer .footer_item {
  font-size: 14px;
  line-height: 1.5;
  margin-top: 10px;
  display: flex;
  align-items: center;
  margin-left: 40px;
}
.footer .footer_item::after {
  content: "";
  background-color: #fff;
  width: 1px;
  height: 100%;
  display: inline-block;
  transform: translateX(20px);
}
.footer .footer_item:first-of-type {
  margin-left: 0;
}
.footer .footer_item:last-of-type::after {
  display: none;
}
.footer .footer_link {
  color: #fff;
}
.footer .copyright {
  font-size: 12px;
  line-height: 1.75;
  margin-top: 70px;
  display: block;
  text-align: center;
}
@media only screen and (min-width: 960px) {
  .footer .copyright {
    width: 100%;
    text-align: center;
    margin-top: 60px;
  }
}

/* =====================
  END footer
===================== */