@use './mixin/mixin' as *;
@use './setting/setting' as *;
@use './setting/normalize' as *;

body {
  font-family: 'Noto Sans JP', sans-serif, 'ヒラギノ角ゴ ProN', 'Hiragino Kaku Gothic ProN', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
}

/* =====================
  header
===================== */
.header {
  padding: 10px 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 1;
  width: 100%;
  background-color: #fff;
  @include pc {
    width: 300px;
    height: 100vh;
    display: block;
    padding: 40px 45px;
  }
  .header_logo {
    width: 180px;
    @include pc {
      width: 200px;
    }
  }
  .icon_wrapper {
    padding: 10px 0;
    z-index: 1;
    @include pc {
      display: none;
    }
    &:hover {
      cursor: pointer;
    }
  }
  .menu_icon {
    width: 30px;
    height: 2px;
    background-color: #000;
    position: relative;
    transition: background-color 0.3s linear;
    &::before,
    &::after {
      content: "";
      display: block;
      width: 30px;
      height: 2px;
      background-color: #000;
      position: absolute;
      transition: transform 0.3s ease-out;
      transform-origin: center;
    }
    &::before {
      top: -10px;
    }
    &::after {
      top: 10px;
    }
  }
  .menu_content {
    @include sp {
      background-color: #fff;
      width: 300px;
      height: 100vh;
      position: fixed;
      left: 0px;
      top: 0;
      padding: 25px;
      transform: translateX(-300px);
      opacity: 0;
      overflow: hidden;
      transition: transform 0.3s linear, opacity 0.3s linear;
    }
    @include pc {
      margin-top: 38px;
    }
    .menu_item {
      font-size: 14px;
      line-height: lh(24, 14);
      margin-top: 16px;
      @include pc {
      }
    }
    .sns_list {
      margin-top: 78px;
      @include pc {
      }
    }
    .sns_item {
      font-size: 14px;
      line-height: lh(24, 14);
      margin-top: 16px;
      @include pc {
      }
    }
  }
  &.is-open {
    .mask {
      display: block;
    }
    .menu_icon {
      background-color: transparent;
      &::before {
        background-color: #fff;
        transform: rotate(-315deg) translate(7px, 7px);
      }
      &::after {
        background-color: #fff;
        transform: rotate(315deg) translate(7px, -7px);
      }
    }
    .menu_content {
      @include sp {
        transform: translateX(0px);
        opacity: 1;
        overflow: visible;
      }
    } 
  }
}
/* =====================
  END header
===================== */

.mask {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0,0,0,0.8);
}

.content_wrapper {
  @include pc {
    margin-left: 300px;
  }
}

/* =====================
  mainVisual
===================== */
.mainVisual {
  height: 70vh;
  @include pc {
    height: 100vh;
  }
  .mainVisual_video {
    height: 600px;
    position: fixed;
    z-index: 0;
    @include pc {
      width: 100vw;
      object-fit: cover;
    }
  }
}
/* =====================
  END mainVisual
===================== */

/* =====================
  introduction
===================== */
.introduction {
  position: relative;
  z-index: 0;
  background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.8));
  .introduction_wrapper {
    padding: 60px 20px 0;
    @include pc {
      max-width: 900px;
      margin: auto;
    }
  }
  .introduction_title {
    @include title;
  }
  .introduction_list {
    @include pc {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-top: 10px;
    }
    .introduction_item {
      margin-top: 30px;
      position: relative;
      border: 1px solid #fff;
      @include pc {
        width: calc(100% / 2 - 28px);
        margin-top: 50px;
      }
    }
    .introduction_link {
      display: block;
      &::after {
        content: "";
        box-shadow: inset 0px -30px 0px #2e2e2e;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
      }
    }
    .introduction_text {
      color: #fff;
      position: absolute;
      bottom: 4px;
      left: 7px;
      z-index: 1;
      font-size: 12px;
      line-height: lh(24, 12);
    }
  }
}
/* =====================
  END introduction
===================== */

/* =====================
  feature
===================== */
.feature {
  background-color: rgba(0,0,0,0.8);
  position: relative;
  .feature_wrapper {
    padding: 120px 20px;
    @include pc {
      padding: 160px 20px;
      max-width: 900px;
      margin: auto;
    }
  }
  .feature_title {
    @include title;
  }
  .feature_list {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    color: #fff;
    @include pc {
      margin-top: 60px;
    }
    .feature_item {
      width: calc(100% / 3 - 11px);
      text-align: center;
      @include pc {
        width: calc(100% / 3 - 86px / 3);
      }
      .feature_link {
        color: #fff;
        border: 1px solid #fff;
        border-radius: 50%;
        display: inline-block;
        width: 100%;
        padding: calc(50% - 10px) 0;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        overflow: hidden;
        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 0;
          height: 100%;
          background-color: #fff;
          opacity: 0.3;
          transition: width 0.3s linear;
        }
        &:hover::after {
          width: 100%;
        }
      }
      .feature_item_title {
        margin-top: 15px;
        font-size: 16px;
        line-height: lh(21, 16);
      }
      .feature_description {
        font-size: 14px;
        margin-top: 5px;
        line-height: lh(21, 14);
      }
    }
  }
}
/* =====================
  END feature
===================== */

/* =====================
  flow
===================== */
.flow {
  padding: 60px 20px 0;
  background-color: #fff;
  position: relative;
  @include pc {
    padding: 120px 0 0;
  }
  .flow_wrapper {
    @include pc {
      max-width: 900px;
      margin: auto;
      padding: 0 20px 30px;
    }
  }
  .flow_title {
    @include title;
    color: #000;
    &::before,
    &::after {
      background-color: #000;
    }
  }
  .flow_description {
    margin-top: 30px;
    font-size: 16px;
    line-height: lh(24, 16);
    @include pc {
      margin-top: 60px;
    }
  }
  .flow_inner {
    @include pc {
      display: flex;
      margin-top: 60px;
    }
  }
  .flow_image_list {
    padding: 0 20px;
    position: relative;
    @include pc {
      min-width: 200px;
      padding: 0;
      height: 100%;
    }
    &::before {
      content: "";
      background-color: #FFDD00;
      width: 50px;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
    }
  }
  .flow_image_item {
    margin-top: 60px;
    background-color: #414141;
    position: relative;
    padding: 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    &:first-of-type {
      margin-top: 30px;
      @include pc {
        margin-top: 0;
      }
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -30px;
      border-style: solid;
      border-color: #414141 transparent transparent transparent;
      border-width: 30px calc(50vw - 40px) 0 calc(50vw - 40px);
      @include pc {
        border-width: 30px 100px 0 100px;
      }
    }
  }
  .flow_image_phase {
    font-size: 14px;
    line-height: lh(21, 14);
    color: #fff;
  }
  .flow_detail {
    margin: 80px 0 0 10%;
    @include pc {
      margin-top: 0;
    }
  }
  .flow_detail_item {
    margin-top: 43px;
    &:first-of-type {
      margin-top: 0;
    }
  }
  .flow_detail_title {
    font-size: 20px;
    line-height: lh(21, 20);
    font-weight: normal;
    text-indent: -0.7rem;
    display: flex;
    align-items: baseline;
    border-bottom: 1px solid #000;
  }
  .number {
    color: #fff;
    background-color: #414141;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-indent: 0;
    margin: 0 20px 0 -45px;
  }
  .flow_detail_description {
    font-size: 16px;
    line-height: lh(24, 16);
    padding-top: 5px;
  }
}
/* =====================
  END flow
===================== */

/* =====================
  contact
===================== */
.contact {
  padding: 120px 20px;
  background-color: #fff;
  position: relative;
  @include pc {
    padding: 146px 0 160px;
  }
  .contact_wrapper {
    max-width: 900px;
    margin: auto;
    padding: 0 20px;
  }
  .contact_title {
    @include title;
    color: #000;
    &::before,
    &::after {
      background-color: #000;
    }
  }
  .contact_description {
    margin-top: 30px;
    font-size: 16px;
    line-height: lh(24, 16);
    @include pc {
      margin-top: 60px;
    }
  }
  .contact_list {
    margin-top: 10px;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    @include pc {
      margin-top: 40px;
      display: flex;
      justify-content: space-between;
    }
  }
  .contact_item {
    margin-top: 20px;
    width: 60%;
    border: 1px solid #000;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: outline 0.1s linear;
    @include pc {
      width: calc(100% / 3 - 86px / 3);
    }
    &:hover {
      outline: solid 3px #000;
    }
  }
  .contact_link {
    display: inline-block;
    padding: 50px 0;
    width: 100%;
    position: relative;
    &::after {
      content: "";
      border-style: solid;
      border-color: transparent transparent #000 transparent;
      border-width: 0px 0px 20px 20px;
      position: absolute;
      bottom: 4px;
      right: 4px;
    }
  }
  .contact_image {
    width: 64px;
    height: 64px;
  }
  .contact_text {
    margin-top: 5px;
    font-size: 14px;
    line-height: lh(21, 14);
  }
}
/* =====================
  END contact
===================== */

/* =====================
  scroll
===================== */
.scroll {
  width: 50px;
  height: 50px;
  border: 1px solid #fff;
  border-radius: 50%;
  background-color: #121212;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  position: fixed;
  bottom: 25px;
  right: 25px;
  opacity: 0;
  overflow: hidden;
  transition: opacity 0.3s linear;
  &::after {
    content: "";
    border-style: solid;
    border-color: transparent transparent #fff transparent;
    border-width: 0px 7px 7px 7px;
    display: inline-block;
  }
  &.is-show {
    opacity: 1;
    overflow: visible;
  }
}
/* =====================
  END scroll
===================== */

/* =====================
  footer
===================== */
.footer {
  background-color: #121212;
  color: #fff;
  padding: 40px 0 20px;
  position: relative;
  z-index: 0;
  .footer_list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .footer_item {
    font-size: 14px;
    line-height: lh(21, 14);
    margin-top: 10px;
    display: flex;
    align-items: center;
    margin-left: 40px;
    &::after {
      content: "";
      background-color: #fff;
      width: 1px;
      height: 100%;
      display: inline-block;
      transform: translateX(20px);
    }
    &:first-of-type {
      margin-left: 0;
    }
    &:last-of-type::after {
      display: none;
    }
  }
  .footer_link {
    color: #fff;
  }
  .copyright {
    font-size: 12px;
    line-height: lh(21, 12);
    margin-top: 70px;
    display: block;
    text-align: center;
    @include pc {
      width: 100%;
      text-align: center;
      margin-top: 60px;
    }
  }
}
/* =====================
  END footer
===================== */