@use '../setting/setting' as s;
@use "sass:math";

// メディアクエリ
@mixin pc {
  @media only screen and (min-width: #{s.$bp-pc}) {
    @content;
  }
}

@mixin sp {
  @media only screen and (max-width: #{s.$bp-sp}) {
    @content;
  }
}

// デバイス毎に表示をコントロールするクラス
.mod-displayNone_mobile {
  @include sp {
    display: none !important;
  }
}

.mod-displayNone_desktop {
  @include pc {
    display: none !important;
  }
}

@function getSize( $size , $width: 599 ) {
  @return #{ $size / $width * 100 } + 'vw';
}

@function lh($tracking,$fontsize) {
  @return math.div($tracking, $fontsize);
}

// タイトル
@mixin title {
  text-align: center;
  font-size: 20px;
  line-height: lh(21, 20);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
  text-shadow: 0px 0px 2px #0d0d0d29;
  @include pc {
    font-size: 28px;
  }
  &::before,
  &::after {
    content: "";
    width: 20%;
    height: 1px;
    background-color: #fff;
    @include pc {
      width: 28%;
    }
  }
}